<template>
  <div class="vantImg_part">
    <van-image 
      :width="finalOption.width" 
      :height="finalOption.height" 
      :lazy-load="finalOption.lazyLoad"
      :fit="finalOption.fit" 
      :src="finalOption.src" 
      :loading-icon="finalOption.loadingImg" 
      :round="finalOption.round">
      <template v-slot:error>
        <img :src="finalOption.errorImg" class="err_img">
      </template>
    </van-image>

  </div>
</template>

<script>
export default {
  name: "NavBar",
  props: {
    options: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {
      finalOption: {
        width: "100%",
        height: "100%",
        lazyLoad: false,
        round: false,
        src: "",
        fit: "cover",
        errorImg: require("@/assets/images/errorDefault.png"),
        loadingImg:  require("@/assets/images/errorDefault.png")
      },
    };
  },
  mounted() {
    let finalOption = this.finalOption;
    Object.assign(finalOption, this.options);
    this.finalOption = finalOption;
  },
};
</script>
<style lang="scss" src='./index.scss'></style>



